.skillscontainer
{
    margin:200px 0;
    display:flex;
    align-items: center;
    justify-content: center;
    width:100%;
    min-height: 100vh;
    position: relative;

    
    
}
.skillscontainer__text
{
    color:white;
    flex:0.6;
    text-align: left;
    max-width: 500px;
}
.skillscontainer__text > h2{
    font-size: 30px;
}
.skillcontainer__image > img{
flex: 0.4;
margin-right: 100px;
width:500px;
object-fit: contain;
border-radius: 5px;
margin-top: 20px;
}
.skillcontainer__slider > .MuiLinearProgress-colorPrimary
{
background-color:gold;
}

.skillcontainer__skillset > h5{
    font-weight: 500;
    padding-top:10px;
    padding-bottom: 15px;

}

.clip1{
    position: absolute;
    top:-30%;
    left:0;
    width:100px;
    height:100px;
    background-color: white;
    opacity: 0.7;
    animation:clip1 25s linear infinite;
    z-index: -1;

}
@keyframes clip1{
    to{
        transform: translate(100vw, 100vh);

    }
}

.clip2{
    position: absolute;
    top:200px;
    left: -50px;
    width:100px;
    border-radius: 50%;
    height:100px;
    background-color: yellow;
    opacity: 0.7;
    animation:clip2 25s linear infinite;
    z-index: -1;

}
@keyframes clip2{
    to{
        
        transform: translate(100vw, -50vh);

    }
}

@media screen and (max-width:468px){
    .skillscontainer{
        flex-direction: column;
        width:100%;
    }
    .skillcontainer__image > img{
        width:90%;
        margin-left:20px;
    }
    .skillscontainer__text{
        width: 80%;
        margin-top: 60px;
    }
}




















