.card__container{
    display: flex;
    justify-content: center;
    align-items: center;
    color:white;
    flex-direction: column;
    width:100%;
}
.card__container h1{
    margin-bottom: 20px;
}
.card{
    position: relative;
    width:400px;
    height:400px;
    color:black;
    background-color: #fff;
    transform-style: preserve-3d;
    transform: perspective(1000px);
    box-shadow: inset 10px 20px 40px rgba(0,0,0,0.25);
    margin-top: 70px;
    border-radius: 30px 0 30px 0;
    

}
.card .details{
   position: absolute;
   top:0;
   left:0;
   width:100%;
   height:100%;
   padding:20px;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}

.details h2{
    margin-bottom: 30px;
}
.details p{
    font-family: italic;
    color:red;
    font-size: 18px;
    text-align: center;
    font-weight: 900;
    margin-right: 20px;

}
.btn2{
    padding:15px;
    border: none;
    outline: none;
    font-weight: bolder;
    color:white;
    background-color: black;
    margin-top: 30px;
    border-radius: 10px;
}
.btn2:hover{
    background-color: crimson;
    letter-spacing: 3px;
    transition: 2s ease-in-out;
}
.card .img-box{

    position: relative;
    width:100%;
    height:100%;
    z-index: 1;
    background-color:transparent;
    transform-origin:left ;
    transform-style: preserve-3d;
    transition: 1s;
   

}
.card:hover .img-box{
    transform: rotateY(-180deg);
}




.card .img-box img{
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    object-fit: cover;
    transform-style: preserve-3d;
    border-radius: 30px 0 30px 0;
    
}

@media screen and (max-width:468px){

    .card__container h1{
        font-size: 16px;
    }
    .card{
        width:300px;
    }
}

