.contact{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin:200px 0 100px;
    color:white;
    position: relative;
}
.contact__container
{
    border:1px solid white;
    width:600px;
    height:200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding:30px;
    margin-top: 20px;
    
}
.contact__container > p{
    font-size:20px;
    text-align: left;
    padding-bottom: 30px;
}
.contact__container > p >span{
    color:red;
}
.contact__icons > a > button > span > .MuiSvgIcon-root
{
    color:white;
    font-size: 30px;
}

.clip1{
    position: absolute;
    top:-30%;
    left:0;
    width:100px;
    height:100px;
    background-color: white;
    opacity: 0.7;
    animation:clip1 25s linear infinite;
    z-index: -1;

}
@keyframes clip1{
    to{
        transform: translate(100vw, 100vh);

    }
}

.clip2{
    position: absolute;
    top:200px;
    left: -50px;
    width:100px;
    border-radius: 50%;
    height:100px;
    background-color: yellow;
    opacity: 0.7;
    animation:clip2 25s linear infinite;
    z-index: -1;

}
@keyframes clip2{
    to{
        
        transform: translate(100vw, -50vh);

    }
}
@media screen and (max-width:468px){

    .contact__container{
        width:80%;
    }
}









