.topcontent{
    color:white;
    width:100%;
    min-height: 100vh;
  
    
}
.top-container
{
position: absolute;
top:250px;
right:200px;

}
.top-container > p{
    letter-spacing: 2px;
    padding-top: 20px;
    text-align: left;
    color:red;
    font-weight:bold;
    font-size:20px;
}
.top-container > a> button{
    margin-bottom: 30px;
    border-radius: 5px;
    margin-top: 15px;
    border:none;
    outline:none;
}
.top-container > h1 {
    font-size: 50px;
    font-family: Arial, Helvetica, sans-serif;

}
.top-container > a > .btn1{
    background-color: orange;
    padding:10px 30px;
    color:white;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;

}
.top-container > a > .btn2
{
    color:white;
    border:1px solid white;
    padding:9px 40px;
    background: transparent;
    font-size: 14px;
    font-weight: 400;
    cursor:pointer;

}
.top-container > a > .btn1:hover{
    color:#f64c08;
    background: white;
    font-weight: bold;
    font-size: 14px;

}
.top-container > a > .btn2:hover{
    color:#8b7870;
    background: white;
    font-weight: bold;
    font-size: 14px;

}

@media screen and (max-width:468px){


    .topcontent{
   
width:10%;
margin-right: 50px;
   
}

.top-container > h1{
    font-size: 26px;
}
.top-container {
    
    width:10%;
}


}
















