.header
{
    display:flex;
    align-items: center;
    color:white;
    background-color: black;
    top:0;
    position: sticky;
    z-index: 2000;
}
.header__left
{
    flex:0.4;
}
.header__left > h1
{
  text-align: center;  
  font-family: 'poppins';
  letter-spacing: 1px;
  padding:20px 0;

}
.header__left > h1 > span{
    color:#f64c08;
}

.icons{
    display:none;
}

.header__right{
    display:flex;
    flex:0.6;
    justify-content: space-evenly;
    align-items: center;
}
.header__right > a >h4{
    font-size: 18px;
    cursor: pointer;

}

.header__right > a >h4:hover
{
    transform: scale(1.09);
    background:#f64c08;
    color:white;
    padding: 5px 20px;
    letter-spacing: 1px;
    font-family: sans-serif;
    border-radius: 2px;
    font-family: 18px;
    cursor: pointer;
}
.header__rightButton
{
    background-color:#f64c08;
    color:white;
    padding:5px 20px;
    cursor: pointer;
    border-radius: 5px;
    
}
 .icons  .fa-bars{
    color:white;
    font-size: 25px;
       
}


.icons  .fa-times{
    color:white;
    font-size: 25px;
    
}





@media screen and (max-width:468px){


   
.header__left{
    margin-left: 25px;

}
.icons{
  
   display:block;
   margin-left: 80px;
}

.header__right{
    flex-direction: column;
    width:100%;
    position: absolute;
    padding:20px;
    right:0px;
    top:64px;
    background-color:rgba(0,0,0,0.7);
    transition: all 0.5s ease-in-out;
}

.header__right > a >h4{
    width:100px;
    display: block;
    margin: auto;
    margin-bottom: 20px;
    text-align: center;
}

.header__right.close{
    right:-100%;
    transition: all 0.5s ease-in-out;
}

}





